  exports.default = Ember.HTMLBars.template({ "id": "v5PXFBvt", "block": "{\"symbols\":[],\"statements\":[[7,\"h2\"],[9],[0,\"MaxiInstruments\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nMaxiInstruments is a class of simple synths and samplers that are designed to so that their parameters can be easily controlled using the \"],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/guides/learner\"],[9],[0,\"Learner.js\"],[10],[0,\" library. They are AudioWorklets backed so do not get interrupted by beefy feature extractors one might use an an input or the running of a model to do the mapping.\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIf you would like to check out the JSDoc API documentation, or the look at the source code, see the links below.\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  \"],[7,\"ul\"],[9],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"\\n      \"],[7,\"a\"],[11,\"href\",\"https://www.doc.gold.ac.uk/~lmcca002/MaxiInstruments.html\"],[9],[0,\"Documentation\"],[10],[0,\"\\n    \"],[10],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"\\n      \"],[7,\"a\"],[11,\"href\",\"https://github.com/Louismac/learnerjs\"],[9],[0,\"Github\"],[10],[0,\"\\n    \"],[10],[0,\"\\n  \"],[10],[0,\"\\n\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nNow (as of October 2021), MaxiInstruments will work in \"],[7,\"strong\"],[9],[0,\"Google Chrome and Mozilla Firefox\"],[10],[0,\".\\n\"],[10],[0,\"\\n\\n\"],[2,\" <p class = \\\"tutorial-text\\\">\\nPreviously, Chromium-based browsers (around Chrome 79) had a bug that may cause slight crackling in Audio Worklet based programs. However, we now believe this bug to be fixed (Chrome 80 onwards). If you are experiencing this issue, <a href = \\\"https://bugs.chromium.org/p/chromium/issues/detail?id=1033493#c42\\\">you can follow the bug </a>and also download and run <a href =\\\"https://www.google.com/chrome/canary/\\\" >Canary (newer beta version of Chrome)</a> or <a href = \\\"https://www.slimjet.com/chrome/google-chrome-old-version.php\\\">older versions of Chrome</a> to get better results.\\n</p> \"],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[9],[0,\"\\nA Simple Example\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nBefore we look at any of the code, we're going to look at a bare bones MaxiInstruments Synthesiser project. No Machine Learning yet. Just vibes.\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou'll see you get a lovely synth with a bunch of parameters to play around with\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n\"],[7,\"ol\"],[9],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"Unmute it!\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"Try changing the main oscillator (top left)\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"Play around with the parameters, you can try\\n    \"],[7,\"ul\"],[9],[0,\"\\n      \"],[7,\"li\"],[9],[0,\"The amplitude envelope (attack, decay, sustain, release)\"],[10],[0,\"\\n      \"],[7,\"li\"],[9],[0,\"LFO on the pitch, filter and amplitude\"],[10],[0,\"\\n      \"],[7,\"li\"],[9],[0,\"Reverb and Delay effects.\"],[10],[0,\"\\n    \"],[10],[0,\"\\n  \"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"If you're unsure of what all the controls do, try some of the Presets \"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\\n\"],[1,[29,\"embedded-project\",null,[[\"docId\",\"height\",\"manualLoad\"],[\"20d53be4-d662-1f0b-faae-a45114374ed3\",\"700px\",false]]],false],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[9],[0,\"\\nContents\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n\"],[7,\"ul\"],[9],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-library\"],[9],[0,\"The Library\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-setup\"],[9],[0,\"Setup\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-gui\"],[9],[0,\"Making the GUI\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-modules\"],[9],[0,\"Loading the Modules\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-Instrument\"],[9],[0,\"Creating an Instrument\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-song\"],[9],[0,\"Playing a Song\"],[10],[10],[0,\"\\n\"],[7,\"ul\"],[9],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-clock\"],[9],[0,\"The Clock\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-sequence\"],[9],[0,\"Setting a Sequence\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-loops\"],[9],[0,\"Loops\"],[10],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-ml\"],[9],[0,\"Controlling with Machine Learning\"],[10],[10],[0,\"\\n\"],[7,\"ul\"],[9],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-model\"],[9],[0,\"Adding the model\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-inputting\"],[9],[0,\"Inputting Data\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-updating-synth\"],[9],[0,\"Updating the Synth\"],[10],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-sampler\"],[9],[0,\"MaxiSampler\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-advanced\"],[9],[0,\"Advanced\"],[10],[10],[0,\"\\n\"],[7,\"ul\"],[9],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-manual-triggers\"],[9],[0,\"Manual Triggers\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-hijacking-clock\"],[9],[0,\"Hijacking the Clock\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-custom-synth\"],[9],[0,\"Writing your own synth\"],[10],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-magenta\"],[9],[0,\"Playing Magenta Sequences\"],[10],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"#learner-examples\"],[9],[0,\"More Examples\"],[10],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-learning-library\"],[9],[0,\"\\nLearning the Library\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  If you hit the \"],[7,\"strong\"],[9],[0,\"Show Code\"],[10],[0,\" button in the bottom right on the project above, you can see how its made. Its not a lot of code! The rest of this document will go through this code from the MaxiInstruments library step by step and have you making amazing projects in no time.\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou can follow along looking at the code above, or select \"],[7,\"strong\"],[9],[0,\"Go To Project\"],[10],[0,\" (the big green button) to check out the project in a separate tab. Otherwise, you can make a new project from scratch and follow along that way.\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-setup\"],[9],[0,\"\\n1. Set up\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe MaxiInstruments.js library lives on the Mimic website and the first thing we have to do is include the library in our document. You can add the following code to the HTML at the top of your project.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n<script src = \\\"https://mimicproject.com/libs/maxiInstruments.v.0.7.js\\\"></script>\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nNow some Javascript! First thing we need to do is make an instance of the main MaxiInstruments object and save it in a variable to refer to later.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nconst maxiInstruments = new MaxiInstruments();\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-gui\"],[9],[0,\"\\n2. Add the GUI\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIn order to get all the useful interfaces in our project, we need to tell MaxiInstruments.js which \"],[7,\"strong\"],[9],[0,\"existing HTML element in our project\"],[10],[0,\" to attach the synths and samplers to when we make them.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\ninstruments.guiElement = document.getElementById(\\\"synths\\\");\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-modules\"],[9],[0,\"\\n3. Load the modules\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nBefore we get down to making our instruments we first need to load the audio worklets processor. To make sure all this set up is done before we get going, we make use of some \"],[7,\"strong\"],[9],[0,\"asynchronous\"],[10],[0,\" functionality in Javascript called \"],[7,\"a\"],[11,\"href\",\"\"],[9],[0,\"Promises\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  You don't need to get into the details of this if you don't want! The important thing to know is that after calling \"],[7,\"code\"],[9],[0,\"instruments.loadModules()\"],[10],[0,\", we pass in another function that gets called \"],[7,\"strong\"],[9],[0,\"after\"],[10],[0,\" the modules have been loaded.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Basically, all of the fun stuff happens \"],[7,\"strong\"],[9],[0,\"within this function\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\ninstruments.loadModules().then(()=> {\\n\\n//Add instruments here!!\\n\\n});\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-instrument\"],[9],[0,\"\\n4. Creating An Instrument\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe main \"],[7,\"code\"],[9],[0,\"MaxiInstruments\"],[10],[0,\" object in a container for all of your instruments, meaning they can share some functionality, like a global clock to keep them all in time for instance! At the moment there is one synthesiser, \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\" (a simple subtractive synthesiser), and \"],[7,\"code\"],[9],[0,\"MaxiSampler\"],[10],[0,\" (which is surprisingly, a sampler!).\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  We'll cover the \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\", as thats what we've been looking at so far. By default, it is a \"],[7,\"strong\"],[9],[0,\"8 voice polyphonic synthesiser\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nUse the code below to add one synth. You can add up to 8 to a single \"],[7,\"code\"],[9],[0,\"MaxiInstruments\"],[10],[0,\" object, depending on how much your set up can handle. Each will have its own GUI.\\n\\nThe constructor returns the synth, but you can also access it from the \"],[7,\"code\"],[9],[0,\"instruments.synths\"],[10],[0,\" property.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nconst synth = instruments.addSynth();\\n//Also can be accessed from instruments.synths[0];\\n\\nconst synth2 = instruments.addSynth();\\n//Also can be accessed from instruments.synths[1];\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-song\"],[9],[0,\"\\n5. Playing a Song\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-clock\"],[9],[0,\"\\nThe Clock\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThere is a global clock that lives in \"],[7,\"code\"],[9],[0,\"MaxiInstruments\"],[10],[0,\" object and is shared by all the synths and samplers associated with it. You can set the tempo in bpm.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe clock allows for sequencing at the level of \"],[7,\"strong\"],[9],[0,\"ticks\"],[10],[0,\", with the default, minimal resolution of \"],[7,\"strong\"],[9],[0,\"24 ticks per beat\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\ninstruments.setTempo(80);\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-sequence\"],[9],[0,\"\\nSetting a Sequence\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIn the example above, we are playing a loop of a few chords. To do this, we have made a sequence and set it to the specific instrument that we want to play it (in this case the first synth).\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nA sequence is an \"],[7,\"strong\"],[9],[0,\"array of note objects\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nEach note object is a Javascript object with some of the following properties. You can use the full name or just initial them.\\n\"],[7,\"ul\"],[9],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"pitch or p: MIDI pitch of the note. Can be a single value or array of values\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"freq or f: Frequency in Hz (an alternative to pitch). Can be a single value or array of values\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"start or s: Start in ticks. Can be a single value or array of values\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"end or e: End in ticks\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"length or l: Length in ticks (an alternative to end)\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"velocity or v: Velocity (optional) (0-127)\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\\t\"],[7,\"code\"],[9],[0,\"\\nconst synthSeq = [\\n    //3 notes at MIDI note 60 with given start and end times (in ticks)\\n    {pitch:60, start:[0, 2, 4], end:[4, 6, 8], velocity:127},\\n    //1 notes at MIDI note 67 with given start and length time (in ticks)\\n    {p:67, s:49, l:34, v:60},\\n    //3 notes at MIDI notes 67, 60, and 70 with given start and length time (in ticks)\\n    {p:[67, 60, 70], s:49, l:34, v:60},\\n    //3 notes at frequency 440, 606 and 200 with given start and end time (in ticks)\\n    {freq:[440, 606, 2000], start:49, e:134},\\n  ];\\nsynth.setSequence(synthSeq);\\n\\t\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nWhen we've built up our \"],[7,\"strong\"],[9],[0,\"array of note objects\"],[10],[0,\", we can then use the \"],[7,\"code\"],[9],[0,\"setSequence()\"],[10],[0,\" to play it on one of our \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\" objects.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIn the example below, we have passed a second, optional parameter. If we dont provide this, any lengths or positions given in the sequence will be interpretted at the default \"],[7,\"strong\"],[9],[0,\"24 ticks per beat\"],[10],[0,\". However, if you can also state a ticks per beat yourself. In the example at the top, we have used \"],[7,\"strong\"],[9],[0,\"4 ticks per beat (1/16ths)\"],[10],[0,\", because don't require more than a resolution of 1/16ths.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\\t\"],[7,\"code\"],[9],[0,\"\\nsynth.setSequence(synthSeq, 4); //4 ticks per beat (1/16ths)\\n\\t\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-loops\"],[9],[0,\"\\nLoops\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nFinally, you can set your sequence to loop round at a given length. You can set the loop on the main \"],[7,\"code\"],[9],[0,\"MaxiInstruments\"],[10],[0,\" object, or you can have individual loops for \"],[7,\"strong\"],[9],[0,\"each instrument!\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nAs with \"],[7,\"code\"],[9],[0,\"setSequence()\"],[10],[0,\", we can provide a second, optional parameter. If we dont provide this, the loop length will be interpretted at the default \"],[7,\"strong\"],[9],[0,\"24 ticks per beat\"],[10],[0,\". However, if you can also state a ticks per beat yourself.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\\t\"],[7,\"code\"],[9],[0,\"\\ninstruments.setLoop(96); //4 Beats, set all instruments (default 24 ticks per beat)\\ninstruments.setLoop(16, 4); //4 Beats, set all instruments (4 ticks per beat)\\nsyn.setLoop(96); //Or set individual loops for each instrument\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-ml\"],[9],[0,\"\\n6. Controlling Instruments with Machine Learning\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nOne of the best things about the \"],[7,\"code\"],[9],[0,\"MaxiInstruments\"],[10],[0,\" set up is that their parameters are super easy connected to the  \"],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/guides/learner\"],[9],[0,\"Learner.js\"],[10],[0,\" library. This means you can drive the sounds they make from a variety of inputs including video, audio and sensors, via a machine learning model.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[9],[0,\"\\nBody Example\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe example below show how we can take the \"],[7,\"strong\"],[9],[0,\"51 inputs\"],[10],[0,\" from Google's BodyPix skeleton tracker, and use them to control \"],[7,\"strong\"],[9],[0,\"9 different parameters\"],[10],[0,\" of the \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\".\\n\"],[7,\"ol\"],[9],[0,\"\\n  \"],[7,\"li\"],[9],[7,\"strong\"],[9],[0,\"Check the camera is working\"],[10],[0,\". If it is, you will seem an outline of your body in differeny coloured splodges (its a technical term). If not, try refreshing the example with the play/pause buttons in the bottom right.\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[7,\"strong\"],[9],[0,\"Find some sounds you like\"],[10],[0,\". We have picked 9 parameters of the synth to map with the machine learning model. Hit the \"],[7,\"code\"],[9],[0,\"Randomise\"],[10],[0,\" button and you'll see which ones they are\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[7,\"strong\"],[9],[0,\"Record some examples\"],[10],[0,\". Stand in the middle of the screen and hit \"],[7,\"code\"],[9],[0,\"Record\"],[10],[0,\", after a couple of seconds, hit \"],[7,\"code\"],[9],[0,\"Stop\"],[10],[0,\".\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"Now find a new sound you like, and record again alongside \"],[7,\"strong\"],[9],[0,\"a different pose.\"],[10],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"Click \"],[7,\"code\"],[9],[0,\"Train\"],[10],[0,\" to train and start running your model. You should hear the sound change and you move your body!\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[1,[29,\"embedded-project\",null,[[\"docId\",\"height\",\"manualLoad\"],[\"ea907b8a-f71f-0c9a-a93f-f4396894fc80\",\"700px\",false]]],false],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[9],[0,\"\\nLooking at the Code\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[9],[0,\"\\nPicking Your Parameters\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nFirst, you'll decide which parameters you want to map. The \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\" has 20 you can map.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n\"],[7,\"strong\"],[9],[0,\"Pro Tip: \"],[10],[0,\" You can use the \"],[7,\"code\"],[9],[0,\"Print\"],[10],[0,\" button on a \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\" to log all the current values of your synth into the browsers console. You can use this to get a full list of the mappable parameters!\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nEach synth has a \"],[7,\"code\"],[9],[0,\"mapped\"],[10],[0,\" property where we provide an array of the parameters we want to control with \"],[7,\"code\"],[9],[0,\"Learner.js\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nsyn.mapped = [\\\"frequency\\\", \\\"attack\\\"];\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-model\"],[9],[0,\"\\nMaking the Model\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou can now start mapping your chosen parameters to a given input using the Learner.js library. More information about how to set this up can be found in this \"],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/guides/learner\"],[9],[0,\"guide\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nWe will use a \"],[7,\"strong\"],[9],[0,\"Regression Model\"],[10],[0,\" (because all of our parameters are continuous numbers). What we need to find out it how many outputs we need to control and \"],[7,\"code\"],[9],[0,\"MaxiInstruments\"],[10],[0,\" has a method to work this out for us. The second argument of \\\"false\\\" tells Learner.js that you don't need an additional GUI for your regression model, because you have the GUI provided by the instrument.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe final argument (10), tells Learner.js to smooth the output over 10 frames.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nlearner.addRegression(instruments.getNumMappedOutputs(), false, 10);\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-inputting\"],[9],[0,\"\\nInputting the Camera and Synth data\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nTo build up our dataset, we use the Learner.js \"],[7,\"code\"],[9],[0,\"newExample()\"],[10],[0,\" function. We need to make pairs of the current \"],[7,\"strong\"],[9],[0,\"body coordinates (input)\"],[10],[0,\" and \"],[7,\"strong\"],[9],[0,\"synth parameters (output)\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIn order to get the current values from the synthesiser, the \"],[7,\"code\"],[9],[0,\"MaxiInstruments\"],[10],[0,\" object will give you all the current values of the mapped parameters of all your instruments with the \"],[7,\"code\"],[9],[0,\"getMappedOutputs()\"],[10],[0,\" function.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIt is important to note that one piece of code (below) serves two purposes.\\n\"],[7,\"ul\"],[9],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"If you are \"],[7,\"strong\"],[9],[0,\"recording\"],[10],[0,\", every time you add a new example the pairing is stored in the dataset.\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"If you are \"],[7,\"strong\"],[9],[0,\"running\"],[10],[0,\", just the input is used and given to the model, which then provides a new output based on the data it has seen.\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nvar onNewCameraData = (newCameraData)=> {\\n  //Match them with outputs (instrument parameters)\\n  learner.newExample(newCameraData, instruments.getMappedOutputs());\\n}\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-updating-synth\"],[9],[0,\"\\nUpdating the Synth from the Model\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nWhenever the model has new outputs (parameters) in response to new inputs (camera data), it calls the \"],[7,\"code\"],[9],[0,\"onOutput()\"],[10],[0,\" function in Learner.js.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe instruments object can then update the mapped parameters using the \"],[7,\"code\"],[9],[0,\"updateMappedOutputs(data)\"],[10],[0,\" function. This will update your synths sounds, and the user interface. Watch them go!\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nlearner.onOutput = (data)=> {\\n  //Update the instruments parameters with output of model\\n  instruments.updateMappedOutputs(data);\\n}\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou can then pick which parameters of the synth you can like to be controlled by your model by passing an array of names. The code below shows how you would select the frequencies of the two oscillators to be controlled by your regression model.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nsyn.mapped = [\\\"frequency\\\", \\\"frequency2\\\"];\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe synth also has a Randomise button that will select random values for your selected parameters. This can help you find fun sounds when making your own mappings.\\n\"],[10],[0,\" -->\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-sampler\"],[9],[0,\"\\n7. MaxiSampler\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nAs with the other examples, click \"],[7,\"code\"],[9],[0,\"Show Code\"],[10],[0,\" in the bottom right of the example above to check out the code.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nRemeber to \"],[7,\"strong\"],[9],[0,\"Unmute\"],[10],[0,\"! Its at the top of the sampler object.\\n\"],[10],[0,\"\\n\"],[1,[29,\"embedded-project\",null,[[\"docId\",\"height\",\"manualLoad\"],[\"40cfa189-14dc-684b-6349-df1c535b2c0c\",\"700px\",false]]],false],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[9],[0,\"\\nCreating a Sampler\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nAs promised, we also have a sampler! Similarly to the \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\", you can have up to 8 samplers (each can hold 8 samples). They are stored in the instrument object’s \"],[7,\"code\"],[9],[0,\"samplers\"],[10],[0,\" property. Each will have its own GUI, showing the controls for 4 samples at a time.\\n\"],[10],[0,\"\\n\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n  const sampler1 = instruments.addSampler();\\n  //Also can be accessed from instruments.samplers[0];\\n\\n  const sampler2 = instruments.addSampler();\\n  //Also can be accessed from instruments.samplers[1];\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[9],[0,\"\\nLoading a Sample\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nLoad each sample by passing the url and an index (of the 8 slots) to store the sample.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nsam.loadSample(\\\"909.wav\\\", 0);\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[9],[0,\"\\nSequencing a Sampler\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[11,\"id\",\"learner-sequencing-sampler\"],[9],[0,\"\\nThe sequencing is almost exactly the same as with the \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\", however, instead of using \"],[7,\"strong\"],[9],[0,\"MIDI note values\"],[10],[0,\" for the pitch parameter, we use \"],[7,\"strong\"],[9],[0,\"sample indexes\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n  const samplerSeq = [\\n      {pitch:0, start:0, velocity:127}, //play sample 0\\n      {pitch:1, start:49}, //play sample 1\\n      {p:[1,2,3], s:49}, //play samples 1,2 and 3\\n    ],\\n  sam.setSequence(samplerSeq);\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[9],[0,\"\\nMapping a Sampler's Parameters\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nSimilarly to the MaxiSynth, you can then pick which parameters of the synth you can like to be controlled by your model by passing an array of names. Each samples parameters are identified by an underscored index following the name (this is also visible on the GUI).\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nsam.mapped = [\\\"rate_0\\\", \\\"gain_1\\\"];\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading-big\"],[11,\"id\",\"learner-advanced\"],[9],[0,\"\\n8. Advanced MaxiInstrument-ing\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-manual-triggers\"],[9],[0,\"\\nManual Triggers\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nBoth samplers and synths respond to simple noteon, noteoff commands. If you are using controlling the frequency of your synth externally, you should also provide a frequency. \"],[7,\"br\"],[9],[10],[7,\"br\"],[9],[10],[0,\" The second velocity argument is optional.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nsynth.noteon(440, 127);\\nsynth.noteoff(440);\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nFor samplers, noteoffs are not necessary and you provide the index of the sample you wish to trigger. For eaxmple, the code below would trigger the sample in the third slot of the first sampler. You only require a noteon for samplers as they are one-shot.\\n\"],[7,\"br\"],[9],[10],[7,\"br\"],[9],[10],[0,\" The second velocity argument is optional.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nsampler.noteon(2, 60);\\nsampler2.noteon(2);\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-hijacking-clock\"],[9],[0,\"\\nHijacking the Clock\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe MaxiInstruments object also has a callback called onTick(), called on every tick of the clock, return the playHead for each instrument. If you have the same loop for all instruments, you only need to care about the first item in this array, if not the playheads are returned in the order they were added.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nAs the callback is being executed on the main thread, it does not have any of the advantages of the separate audio thread where the clock resides, as such may be effected by interruptions. It relies on the messaging system between threads and whilst reasonably reliable, it is not advised to use this for time critical actions and it is not guaranteed to be sample-accurately-in-sync with events being triggered on the audio thread.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\ninstruments.setOnTick((playHeads)=>{\\n  console.log(playHeads)\\n})\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-custom-synth\"],[9],[0,\"\\nWriting Your Own MaxiSynth\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  From version 0.7.1 onwards, it is also possible to program your own \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\", using any of the digital signal processing functions available from the \"],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/guides/maximJS\"],[9],[7,\"code\"],[9],[0,\"Maximilian.js library\"],[10],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou get all of the advantages of the user interface, polyphonic multi voicing and sequencing, whilst also getting full control of what you do with the information and the sounds you make!\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThe two places you get to customise your \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\" are\\n\"],[7,\"ol\"],[9],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"What happens in the DSP loop\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"What parameters are available to be set from the interface, or by a machine learning model\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Lets take a look at the code in this example below for an incredibly simple synth! We have a synth that plays a \"],[7,\"strong\"],[9],[0,\"constant sine wave\"],[10],[0,\", controlled by a single \"],[7,\"strong\"],[9],[0,\"frequency slider\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[1,[29,\"embedded-project\",null,[[\"docId\",\"height\",\"manualLoad\"],[\"fc23f8ee-2d34-b185-9ad0-c07f89acb282\",\"400px\",false]]],false],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  \"],[7,\"strong\"],[9],[0,\"Extending the \"],[7,\"code\"],[9],[0,\"MaxiSynthProcessorBase\"],[10],[0,\" Class\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  We define our new synth by making a script tag, and declaring a \"],[7,\"strong\"],[9],[0,\"new Javascript class\"],[10],[0,\". This will be like the recipe for how to make our custom synth. We \"],[7,\"strong\"],[9],[0,\"extend\"],[10],[0,\" the \"],[7,\"code\"],[9],[0,\"MaxiSynthProcessorBase\"],[10],[0,\", meaning we get almost all of the functionality we need already. We just need to include some specific functions to overwrite the parts we want to change.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n  <script id = \\\"synthCode\\\">\\n      class MinimalSynth extends MaxiSynthProcessorBase {\\n\\n        ......\\n      }\\n    </script>\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  \"],[7,\"strong\"],[9],[0,\"Intialising objects in the \"],[7,\"code\"],[9],[0,\"setup()\"],[10],[0,\" function\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nWithin this class definition, the first of two functions we will need to write is the \"],[7,\"code\"],[9],[0,\"setup()\"],[10],[0,\" function.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThis is run \"],[7,\"strong\"],[9],[0,\"once\"],[10],[0,\" at the beginning, and is best used for initialising \"],[7,\"code\"],[9],[0,\"Maximilian\"],[10],[0,\" objects (such as oscillators, filters, envelopes or effects), or other variables that we will need to use over multiple calls to the sample loop.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nHere, we make a \"],[7,\"code\"],[9],[0,\"maxiOsc()\"],[10],[0,\" object (to be our main sound source), and save it in a variable called \"],[7,\"code\"],[9],[0,\"this.osc\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n  <script id = \\\"synthCode\\\">\\n      class MinimalSynth extends MaxiSynthProcessorBase {\\n\\n        //Called once at begining (declare custom variables)\\n        setup() {\\n    \\t     this.osc = new Maximilian.maxiOsc()\\n        }\\n\\n        ......\\n      }\\n    </script>\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  \"],[7,\"strong\"],[9],[0,\"Returning a signal in the \"],[7,\"code\"],[9],[0,\"endSignal()\"],[10],[0,\" function\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Next we define the \"],[7,\"code\"],[9],[0,\"endSignal()\"],[10],[0,\" function. This is called \"],[7,\"strong\"],[9],[0,\"every sample\"],[10],[0,\" and is where we generate and return our action sound signal.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Here, we generate a sinewave at 440Hz and return it as both \"],[7,\"strong\"],[9],[0,\"left and right channels\"],[10],[0,\" as an array.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n  <script id = \\\"synthCode\\\">\\n      class MinimalSynth extends MaxiSynthProcessorBase {\\n\\n        ......\\n\\n        //Called once at end of process loop (every sample)\\n        endSignal() {\\n          const signal = this.osc.sinewave(440)\\n          return [signal, signal];\\n        }\\n      }\\n    </script>\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  \"],[7,\"strong\"],[9],[0,\"Defining Parameters\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  This of course is not super interesting, and ideally we'd like some way of \"],[7,\"strong\"],[9],[0,\"controlling the frequency\"],[10],[0,\" of the oscillator.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  We can add any number of parameters to our synth and these will\\n  \"],[7,\"ul\"],[9],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"Show up on the synthesiser interface as a slider.\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"Be controllable via machine learning using the \"],[7,\"code\"],[9],[0,\"Learner.js\"],[10],[0,\" library.\"],[10],[0,\"\\n  \"],[7,\"li\"],[9],[0,\"Be accessible on the audio thread in your custom synth code.\"],[10],[0,\"\\n  \"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  The parameters are defined using a Javascript object where each property defines a new parameter.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  For each we provide\\n  \"],[7,\"ul\"],[9],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"A minimum value\"],[10],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"A minimum value\"],[10],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"A default/starting value\"],[10],[0,\"\\n  \"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\\n\"],[7,\"code\"],[9],[0,\"\\n  //Set parameters\\n  const parameters = {\\n    freq:{min:40, max:400, val:100},\\n  }\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  These parameters are then available to use within our audio code as a property of the \"],[7,\"code\"],[9],[0,\"this.parameters\"],[10],[0,\" object.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n  <script id = \\\"synthCode\\\">\\n      class MinimalSynth extends MaxiSynthProcessorBase {\\n\\n        ......\\n\\n        //Called once at end of process loop (every sample)\\n        endSignal() {\\n          const freq = this.parameters.freq.val;\\n          const signal = this.osc.sinewave(freq)\\n          return [signal, signal];\\n        }\\n      }\\n    </script>\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  \"],[7,\"strong\"],[9],[0,\"Passing to the \"],[7,\"code\"],[9],[0,\"addSynth()\"],[10],[0,\" function\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Finally, all we have to do is pass our \"],[7,\"strong\"],[9],[0,\"class definition\"],[10],[0,\" and \"],[7,\"strong\"],[9],[0,\"parameter object\"],[10],[0,\" into MaxiInstruments when we create our synth.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Here, we pass in the \"],[7,\"strong\"],[9],[0,\"id of the script tag\"],[10],[0,\" the class definition is in, and the actual parameter object.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  You could also choose to pass in the \"],[7,\"strong\"],[9],[0,\"url of a text file\"],[10],[0,\" that contained the class definition. On the Mimic platform, this coudl just be the name of a different tab in your project.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n  //Pass custom code and parameters\\n  synth = instruments.addSynth(\\\"synthCode\\\", parameters);\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  \"],[7,\"strong\"],[9],[0,\"Polyphony, Notes and Sequences\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  One of the real advantages of a MaxiSynth is that they are allow for \"],[7,\"strong\"],[9],[0,\"polyphonic sequences\"],[10],[0,\", and in the first synth we described, we didn't take advantage of this at all!\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  As a sequence is played, and notes starts at the same time or overlap, multiple oscillators are used for each voice. The amplitude of each voice is controlled by an individual envelope as they fade in and out as notes are triggered on and off independently.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  To do this is our custom synth, we just need to update our synth definition slightly, overwriting \"],[7,\"strong\"],[9],[0,\"one more function\"],[10],[0,\", \"],[7,\"code\"],[9],[0,\"noteSignal()\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Like \"],[7,\"code\"],[9],[0,\"endSignal()\"],[10],[0,\", this is called \"],[7,\"strong\"],[9],[0,\"every sample\"],[10],[0,\". However, its called \"],[7,\"strong\"],[9],[0,\"once for every currently triggered note\"],[10],[0,\". To account for this, we pass in\\n  \"],[7,\"ul\"],[9],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"The volume of the current ampltiude envelope.\"],[10],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"The index of the oscillator.\"],[10],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"The frequency of the note.\"],[10],[0,\"\\n    \"],[7,\"li\"],[9],[0,\"The volume of that notes individual velocity.\"],[10],[0,\"\\n  \"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  We use all this information, in combination with \"],[7,\"strong\"],[9],[0,\"12 already initialised maxiOsc() objects\"],[10],[0,\" stored in the \"],[7,\"code\"],[9],[0,\"this.oscillators\"],[10],[0,\" property, to generate and return the audio for each note.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Each oscillator also has an accompanying \"],[7,\"strong\"],[9],[0,\"maxiEnvelope() object\"],[10],[0,\" stored in the \"],[7,\"code\"],[9],[0,\"this.adsr\"],[10],[0,\" property. You can use the \"],[7,\"code\"],[9],[0,\"index\"],[10],[0,\" property that is passed into the function to access the oscillators or envelopes where neccessary.\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  Once called for each note, this signal is passed in as an argument to the \"],[7,\"code\"],[9],[0,\"endSignal()\"],[10],[0,\" function, where we can apply any signal processing to the whole signal. For example, we might add \"],[7,\"strong\"],[9],[0,\"reverb\"],[10],[0,\", or a \"],[7,\"strong\"],[9],[0,\"filter\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\n  There is even a \"],[7,\"code\"],[9],[0,\"setupSignal()\"],[10],[0,\" function we can use to do any calculations we need to do \"],[7,\"strong\"],[9],[0,\"before we start calculating the \"],[7,\"code\"],[9],[0,\"noteSignal()\"],[10],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n  <script id = \\\"synthCode\\\">\\n    class MinimalSynth extends MaxiSynthProcessorBase {\\n\\n      ......\\n\\n      //Called once at start of process loop (every sample)\\n      setupSignal() {\\n\\n      }\\n\\n      //Called once for every oscillator currently triggered for polyphony (every sample)\\n      noteSignal(envVol, index, freq, vel) {\\n        let sig = this.oscillators[index].sinewave(freq)\\n        return sig * envVol * vel;\\n      }\\n\\n      //Called once at end of process loop (every sample)\\n      endSignal(noteSignals) {\\n        //Return stereo signal\\n        const gain = this.parameters.gain.val;\\n        return [noteSignals * gain, noteSignals * gain];\\n      }\\n    }\\n    </script>\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIn the example project below, we see how our custom synth code now responds to a \"],[7,\"strong\"],[9],[0,\"polyphonic sequence\"],[10],[0,\". Play around with the attack, decay, sustain and release parameters and here the effect on each notes envelope.\\n\"],[10],[0,\"\\n\"],[1,[29,\"embedded-project\",null,[[\"docId\",\"height\",\"manualLoad\"],[\"18ecbd94-1057-0de1-0ed3-834935922cb3\",\"700px\",false]]],false],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-continuous\"],[9],[0,\"\\nNon-note based Synths\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIf programming the \"],[7,\"code\"],[9],[0,\"MaxiSynth\"],[10],[0,\" using note sequences isn't your jam, you can use it more like a modular oscillator unit. In this mode, it will just output \"],[7,\"strong\"],[9],[0,\"two continuous tones\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou can either sequence the rhythm and not the pitches, or just trigger one \"],[7,\"code\"],[9],[0,\"noteon()\"],[10],[0,\" at the beginning and leave it running. Check out the example for more details\\n\"],[10],[0,\"\\n\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nEach of the frequencies are individually controllable and you'll see two new controls appear on the synthesisers interface (frequency and frequency2). These are also available to map to a Learner.js model!\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\nsyn.useFreqSliders(true);\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[1,[29,\"embedded-project\",null,[[\"docId\",\"height\",\"manualLoad\"],[\"73904a62-b60f-d590-70aa-0495da686a47\",\"700px\",false]]],false],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-heading\"],[11,\"id\",\"learner-magenta\"],[9],[0,\"\\nMagenta Sequences\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou can also input sequences generated from Magenta directly into the synths. Below is an example using one of Magenta's pretrained recurrent neural networks to complete the a given seqeunce, and playing it directly on a MaxiSynth. First import the Magenta library.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\"],[7,\"code\"],[9],[0,\"\\n<script src = \\\"https://cdn.jsdelivr.net/npm/@magenta/music@1.11.0\\\"></script>\\n\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nIn order to get a continued seqeunce from Magenta's pretrained models, you can specify a seed sequence given\\n\"],[7,\"ul\"],[9],[0,\"\\n\\t\"],[7,\"li\"],[9],[0,\"A tempo\"],[10],[0,\"\\n\\t\"],[7,\"li\"],[9],[0,\"A definition of stepsPerQuarter. We define time in steps and this tells us how many represent a quarter note / crotchet\"],[10],[0,\"\\n\\t\"],[7,\"li\"],[9],[0,\"An array of notes objects, including pitch (MIDI), a start time and end time each in steps\"],[10],[0,\"\\n\\t\"],[7,\"li\"],[9],[0,\"A total length, also in steps\"],[10],[0,\"\\n\"],[10],[0,\"\\nSee the code below as an example\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\\t\"],[7,\"code\"],[9],[0,\"\\nconst inputSeq = {\\n  tempos: [{time:0, qpm:80}],\\n  notes:[\\n    {pitch:60, quantizedStartStep:0, quantizedEndStep:2},\\n    {pitch:67, quantizedStartStep:4, quantizedEndStep:6}\\n  ],\\n  quantizationInfo:{stepsPerQuarter: 4},\\n  totalQuantizedSteps:32,\\n};\\n\\t\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nThen you can feed to Magenta, and set the returned sequence directly to synth.\\n\"],[10],[0,\"\\n\"],[7,\"pre\"],[9],[0,\"\\t\"],[7,\"code\"],[9],[0,\"\\nlet rnn = new mm.MusicRNN(MODEL_URL);\\nrnn.initialize().then(()=> {\\n  rnn.continueSequence(inputSeq, 32, 1.5).then((newSeq)=> {\\n\\n    //Set the tempo and loop point\\n    instruments.setTempo(newSeq.tempos[0].qpm);\\n    const loop = 24 / newSeq.quantizationInfo.stepsPerQuarter * newSeq.totalQuantizedSteps;\\n    instruments.setLoop(loop)\\n\\n    //Set the note sequence\\n    syn.setSequence(newSeq);\\n  });\\n});\\n\\t\"],[10],[0,\"\\n\"],[10],[0,\"\"],[7,\"h2\"],[11,\"id\",\"learner-examples\"],[9],[0,\"More Example Projects\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nHere are some examples sequenced in various ways, mostly using body tracking as an input.\\n\"],[7,\"ul\"],[9],[0,\"\\n  \"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/code/73d93516-e0de-a85c-5fc7-c6cc03f4666b\"],[9],[0,\"MIDI\"],[10],[10],[0,\" This example allows you to play notes into the synth using an external MIDI instrument, whilst mapping the parameters to a different input. Note WebMidi is curently only supported in Chrome. You can connect to external devices or connect to your internal MIDI bus, \"],[7,\"a\"],[11,\"href\",\"https://help.ableton.com/hc/en-us/articles/209774225-How-to-setup-a-virtual-MIDI-bus\"],[9],[0,\"this is a good resource for how to do that\"],[10],[0,\". If you were generating notes in an another program (Max/MSP, Supercollider, PD), this would be a good way to trigger MaxiSynth. First refresh devices, then select your MIDI source from the dropdown.\\n  \"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/code/d57c9d9b-284d-9ab3-8118-e7c33eafeeaf\"],[9],[0,\"One shot Sequencer\"],[10],[10],[0,\" This allows you use a one-shot sequencer to program a tune yourself, whilst mapping the parameters of the synths to one of the inputs.\\n  \"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/code/f6bdb7ad-4cb0-8652-0dee-f0c7db9fede5\"],[9],[0,\"Hand coded sequence\"],[10],[10],[0,\" This shows you how you can program in your own sequence by hand.\\n  \"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/code/fa99819f-775c-2552-198c-2340739a1b5c\"],[9],[0,\"Magenta Generated sequence\"],[10],[10],[0,\" This shows you how you can generate a sequence using Google's Magenta models and plug that straight into a synth.\\n  \"],[7,\"li\"],[9],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/code/1cc85746-67d2-0cef-7f69-a238c6d2b489\"],[9],[0,\"MIDI File playback\"],[10],[10],[0,\" This shows you how you can upload a MIDI File as an asset then play on a MaxiSynth.\\n\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou can find a \"],[7,\"a\"],[11,\"href\",\"https://mimicproject.com/inputs\"],[9],[0,\"bunch of fun inputs\"],[10],[0,\"\\n\"],[10],[0,\"\\n\"],[7,\"p\"],[11,\"class\",\"tutorial-text\"],[9],[0,\"\\nYou can also see the source and instructions for running the library locally or in your own projects away from the MIMIC platform in \"],[7,\"a\"],[11,\"href\",\"https://github.com/Louismac/learnerjs\"],[9],[0,\"this repository\"],[10],[0,\".\\n\"],[10],[0,\"\\n\"]],\"hasEval\":false}", "meta": { "moduleName": "ember-share-db/templates/components/maxiinstrument-guide.hbs" } });
